
body {
	font-family: futura-pt,Helvetica,Arial,"Hiragino Sans GB","Hiragino Sans GB W3",Microsoft JhengHei,WenQuanYi Micro Hei,"Microsoft YaHei",sans-serif;
	color: #414141;
	background-color: #ebecee;
	line-height: 1.5em;
	text-rendering: geometricPrecision;
}
#content {
	margin-top: 7rem;
	text-align: left;
	background: #fff;
	-webkit-box-shadow: 0 0 3px rgba(0,0,0,.15);
	        box-shadow: 0 0 3px rgba(0,0,0,.15);
}
article {
	padding: 1rem;
}
h1 {
	line-height: 1em;
}
#left {
	-ms-flex-preferred-size: 4%;
	flex-basis: 4%;
	max-width: 4%;
	min-width: 40px;
}
#section {
	margin-top: 10rem;
}
#section a {
	color: transparent;
	text-decoration: none;
	display: block;
	background: #000;
	width: 2rem;
	height: 2rem;
	border-radius: 10rem;
	margin-top: 6px;
	opacity: .9;
}
#section a:nth-child(1) {
	background-image: url(/static/img/section1.8b70e48.png);
	background-size: 100%;
}
#section a:nth-child(2) {
	background-image: url(/static/img/section2.a585f06.png);
	background-size: 100%;
}
#section a:nth-child(3) {
	background-image: url(/static/img/section3.eed86cd.png);
	background-size: 100%;
}
#section a:nth-child(4) {
	background-image: url(/static/img/section4.e276b3f.png);
	background-size: 100%;
}
#section span {
	display: none;
}
footer {
	text-align: right;
	opacity: .7;
	font-size: .8em;
	margin-top: .2rem;
	margin-bottom: 1rem;
}
a {
	color: #828282;
	cursor: pointer;
	text-decoration: underline;
}
